import Form from '~/components/Form';
import ToggleList from '~/components/ToggleList';

import './test2.css';
import Dropify from '~/components/Dropify/Dropify';

function TestPopUp3() {
	const list = [
		'Thnh vin t mua',
		'Quyn li thnh vin',
		'Thnh vin pht ti v',
		'Hot ng thnh vin',
		'Khc',
	];
	return (
		<>
			<Form isHeaderFlag>
				<ToggleList subListItems={list}>Vn  hi vin</ToggleList>
				<ToggleList subListItems={list}>Vn  hi vin</ToggleList>
				<ToggleList subListItems={list}>Vn  hi vin</ToggleList>
				<ToggleList subListItems={list}>Vn  hi vin</ToggleList>
				<ToggleList subListItems={list}>Vn  hi vin</ToggleList>
				<Dropify title={'Ti nh ln:'}>Drag and drop a files here, or click</Dropify>
			</Form>
		</>
	);
}

export default TestPopUp3;
