import DropDown from '~/components/DropDown';
import Input from '~/components/Input';
import TextArea from '~/components/TextArea';
import * as $ from './Styles';

function PartnerShip() {
	let options = [
		{
			text: 'Hp tc thng mi',
			value: '0',
		},
		{
			text: 'Hp tc qung co',
			value: '1',
		},
		{
			text: 'Hp tc ni dung',
			value: '2',
		},
	];
	return (
		<$.Sheet>
			<$.Main>
				<h2 className="title">Hp tc thng mi</h2>
				<$.Description className={'primary'}>
					Cho d l hp tc v qung co, thng mi, ni dung hay cc  n hp tc v phng din khc, chn
					thnh hoan nghnh bn gi th n nhm m bo cht lng dch v ti u nht, vui lng in hon
					chnh biu mu sau, chng ti s nhanh chng lin h vi bn
				</$.Description>
				<$.Row>
					<Input
						type="text"
						labelText={'H'}
						width="32.6rem"
					/>
					<Input
						type="text"
						labelText={'Tn'}
						width="32.6rem"
					/>
				</$.Row>
				<$.Row>
					<Input
						type="text"
						labelText={'Email'}
						width="66rem"
					/>
				</$.Row>
				<$.Row>
					<DropDown
						labelText={'Loi hnh hp tc'}
						options={options}
						defaultText="Vui lng chn loi hnh hp tc"
						style={{ width: '66rem' }}
					/>
				</$.Row>
				<$.Row>
					<Input
						type="text"
						labelText={'Ch  th'}
						width="66rem"
					/>
				</$.Row>
				<$.Row>
					<TextArea
						type="text"
						labelText={'Ni dung th'}
						width="100%"
						placeholder="Gii hn trong 3000 ch"
						maxlength="3000"
						rows="7"
						cols="75"
					/>
				</$.Row>
			</$.Main>
		</$.Sheet>
	);
}

export default PartnerShip;
