import ToggleList from '~/components/ToggleList';
import * as $ from './Styles';
import * as dataset from './dataset';

function Support() {
	return (
		<$.SuperSheet style={{ marginTop: '10rem', marginBottom: '10rem' }}>
			<$.Sheet>
				<h5>V ti khon</h5>
				<ToggleList subListItems={dataset.account_1}>Lm th no  lin kt email/ST?</ToggleList>
				<ToggleList subListItems={dataset.account_2}>Ti c th xem UID ti khon  u?</ToggleList>
				<ToggleList subListItems={dataset.account_3}>Lm th no  thay i mt khu ti khon?</ToggleList>
				<ToggleList subListItems={dataset.account_4}>
					Lm th no  thay i email/s in thoi lin kt?
				</ToggleList>
				<ToggleList subListItems={dataset.account_5}>Khng th ng nhp vo ti khon?</ToggleList>
				<$.SplitRange />
			</$.Sheet>
			<$.Sheet>
				<h5>V thanh ton</h5>
				<ToggleList subListItems={dataset.payment_1}>C nhng phng thc no  mua VIP?</ToggleList>
				<ToggleList subListItems={dataset.payment_2}>Mua VIP nh th no?</ToggleList>
				<ToggleList subListItems={dataset.payment_3}>Lm th no  quy i m VIP?</ToggleList>
				<ToggleList subListItems={dataset.payment_4}>Lm th no  s dng m u i?</ToggleList>
				<$.SplitRange />
			</$.Sheet>
			<$.Sheet>
				<h5>V s kin</h5>
				<ToggleList subListItems={dataset.event_1}>
					Ti  mua gi khuyn mi, ti sao li b tr ph?
				</ToggleList>
				<ToggleList subListItems={dataset.event_2}>
					Nu ti mua gi khuyn mi, th t cch hi vin trc  ca ti s th no?
				</ToggleList>
				<$.SplitRange />
			</$.Sheet>
		</$.SuperSheet>
	);
}

export default Support;
